header .top_header .top_info {
  margin-left: -5px;
  margin-right: -5px;
}

header .top_header .top_info::after {
  display: block;
  content: "";
  clear: both;
}

header .top-links {
  float: left;
}

header .top-links span {
  margin: 10px 5px;
}

header .sign_in {
  float: left;
  text-align: right;
}

header .top_header_logo .top_logo {
  margin-left: -5px;
  margin-right: -5px;
}

header .top_header_logo .top_logo::after {
  display: block;
  content: "";
  clear: both;
}

header .top_header_logo .top_logo .img_logo {
  float: left;
}

header .top_header_logo .text_logo {
  float: left;
}

header .top_header_logo .top_search {
  float: left;
}

.information .wrapper .items {
  margin-left: -5px;
  margin-right: -5px;
}

.information .wrapper .items::after {
  display: block;
  content: "";
  clear: both;
}

.information .wrapper .items .item {
  float: left;
  text-align: center;
}

.information .wrapper .information_block {
  margin-left: -5px;
  margin-right: -5px;
}

.information .wrapper .information_block::after {
  display: block;
  content: "";
  clear: both;
}

.information .wrapper .information_block .information_block_header {
  float: left;
}

.information .wrapper .information_block .information_block_text {
  float: left;
}

.information .wrapper .block_circle {
  margin-left: -5px;
  margin-right: -5px;
}

.information .wrapper .block_circle::after {
  display: block;
  content: "";
  clear: both;
}

.information .wrapper .block_circle .item {
  float: left;
}

.information .wrapper .block_circle .item a {
  display: inline-table;
}

.news .news_content {
  margin-left: -5px;
  margin-right: -5px;
}

.news .news_content::after {
  display: block;
  content: "";
  clear: both;
}

.news .news_content .news_header {
  float: left;
}

.news .news_content .button_all {
  float: left;
}

.news .news_items {
  float: left;
  margin-left: -5px;
  margin-right: -5px;
}

.news .news_items::after {
  display: block;
  content: "";
  clear: both;
}

.news .news_items .news_item {
  float: left;
  display: block!important;
}

.news .news_items .news_item .subtitle {
  margin-top: 10px;
}

.news .aside_menu {
  float: left;
}

.news .aside_menu .menu_content {
  float: left;
}

footer .footer_content {
  margin-left: -5px;
  margin-right: -5px;
}

footer .footer_content::after {
  display: block;
  content: "";
  clear: both;
}

footer .footer_content .footer_info {
  float: left;
}

footer .footer_content .footer_logo {
  float: left;
}

footer .footer_content .footer_menu {
  float: left;
}

footer .footer_links {
  margin-left: -5px;
  margin-right: -5px;
}

footer .footer_links::after {
  display: block;
  content: "";
  clear: both;
}

footer .footer_links .social_networks {
  float: left;
}

.news_page .news_page_content {
  margin-left: -5px;
  margin-right: -5px;
}

.news_page .news_page_content::after {
  display: block;
  content: "";
  clear: both;
}

.news_page .news_page_content .news_page_items {
  float: left;
}

.news_page .aside_menu {
  float: left;
}

@media screen and (max-width: 992px) {
  .information .wrapper .items .item {
    margin: 10px 10px 10px 10px;
  }
}

@media screen and (max-width: 720px) {
  .information .wrapper .items .item {
    margin: 10px 0px;
  }
}

@media screen and (max-width: 380px) {
  header .top-links span {
    display: none;
  }
}